//转义HTML的方法
function htmlScape(htmlStr) {
    return htmlStr.replace(/<|>|"|&/g, (main) => {
        switch (main) {
            case '<':
                return '&lt';

            case '>':
                return '&gt';

            case '"':
                return '&quot';

            case '&':
                return '&amp';
        }
    })
}

function htmlUnscape(htmlStr) {
    return htmlStr.replace(/&lt|&gt|&quot|&amp/g, (match) => {
        switch (match) {
            case '&lt':
                return '<';
            case '&gt':
                return '>';

            case '&quot':
                return '"';

            case '&amp':
                return '&';
        }
    })
}

module.exports = {
    htmlScape,
    htmlUnscape
}